@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
.NavMain {
  background-color: transparent;
  color: rgb(57, 57, 57);
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 20px;
  height: 70px;
}

.NavMain .nav-list {
  margin-left: auto;
  display: -webkit-flex;
  display: flex;
}

.NavMain .nav-list li:not(:last-child) {
  margin-right: 20px;
}

.logo {
  width: 90px;
  height: 90px;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: "Manrope", sans-serif;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style-type: none;
}

.cards {
  display: grid;
  grid-template-columns: repeat(4,auto);
  grid-row-gap:40px;
  margin-top: 2%;
  margin-left: 35px;
  margin-right: 35px;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-content: center;
          align-content: center;
  border: 2px solid black;
  padding: 20px 20px;
  border-radius: 5px;
  text-align: center;
}

.card-title {
  font-size: 14px;
  font-weight: bold;
  text-align: center;
  padding-bottom: 5%;
}

.image-card {
  width: 70%;
  height: 65%;
  margin-bottom: 10px;
  border-radius: 5px;
}

h1 {
  padding: 5% 0%;
  text-align: center;
  font-size: 50px;
  font-weight: bold;
  color: #C70039;
}

h3 {
  padding: 5% 0%;
  text-align: center;
}

.intro {
  padding: 3% 0%;
}

.add-btn {
  width: 20%;
  margin-bottom: 5%;
}

.input_instructions {
  height: 80px;
  width: 1000px
}
.Form {
    text-align: center;
  }

label {
    width: 110px;
    display: inline-block;
    vertical-align: top;
    margin: 6px;
    font-weight: bold;
  }

  input, textarea {
    width: 400px;
  }

.Form__button {
    margin: 4% 0% 2%;
}
*,*::before,*::after{box-sizing:border-box}ul[class],ol[class]{padding:0}body,h1,h2,h3,h4,p,ul[class],ol[class],figure,blockquote,dl,dd{margin:0}html{scroll-behavior:smooth}body{min-height:100vh;text-rendering:optimizeSpeed;line-height:1.5}ul[class],ol[class]{list-style:none}a:not([class]){-webkit-text-decoration-skip:ink;text-decoration-skip-ink:auto}img{max-width:100%;display:block}article>*+*{margin-top:1em}input,button,textarea,select{font:inherit}img:not([alt]){-webkit-filter:blur(10px);filter:blur(10px)}@media(prefers-reduced-motion:reduce){*{-webkit-animation-duration:.01ms !important;animation-duration:.01ms !important;-webkit-animation-iteration-count:1 !important;animation-iteration-count:1 !important;transition-duration:.01ms !important;scroll-behavior:auto !important}}
